.contacts {

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr;

    @include onDesktop {
      grid-template-columns: 50% 50%;
    }
  }

  &__info-wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 8px;

    @include onDesktop {
      flex-direction: row;
      gap: 111px;
    }
  }

  &__info-title {
    margin-bottom: 8px;
    font-weight: normal;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-value {
    display: inline-block;
    margin-bottom: 24px;
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    color: #3e3e3e;
    text-decoration: none;
    transition: opacity $trnstn300ms;

    &:hover {
      opacity: 0.6;
    }
  }
}
