.form {

  @include onDesktop {
    align-self: center;
  }

  &__input-block {
    margin-bottom: 16px;

  }

  &__input {
    width: 100%;
    min-height: 48px;
    padding: 14px 17px;
    margin-bottom: 16px;
    border: 2px solid transparent;
    font-family: inherit;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: $black;
    background: #f7f8ff;
    border-radius: 8px;
    resize: none;
    cursor: pointer;
    transition: border $trnstn300ms;

    &::-webkit-input-placeholder {
      color: #8f99a8;
    }

    &:hover {
      border: 2px solid #ccd0e0;
    }

    &:focus-visible {
      outline: 2px solid transparent;
    }

    &:focus {
      border: 2px solid $blue-color;
      color: $black;
    }
  }

  &__btn {
    width: 100%;
    height: 56px;
    border: 2px solid $blue-color;
    border-radius: 8px;
    font-weight: bold;
    font-size: 16px;
    line-height: 14px;
    color: $white-color;
    background-color: $blue-color;
    transition: background-color $trnstn300ms, box-shadow $trnstn300ms;

    &:hover {
      cursor: pointer;
      background-color: $medium-blue;
      box-shadow: 0 13px 27px $dark-blue;
      color: $white-color;
    }
    &:active {
      box-shadow: 0 8px 14px $dark-blue;
    }
  }
}
