.footer {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-width: 320px;
  height: 206px;
  padding-bottom: 25px;
  background-image: url(../images/footer.png);
  background-position: center;
  background-size: cover;

  &__logo {
    display: flex;
    align-self: center;
    width: 68px;
    height: 45px;
    background-image: url(../images/logo.png);
    background-size: cover;
    margin: 0 auto;
  }
}
