.nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 20px;
  background-color: $blue-color;
  transform: translateX(-100%);
  transition: transform $trnstn300ms;
  z-index: 1;

  @include onDesktop {
    padding: 120px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin-bottom: 48px;

    @include landscapeMobile {
      margin: 64px 0 5px;
    }

  }
  &__item {
    margin: 11px 0;

    @include landscapeMobile {
      margin: 5px 0;
    }

  }
  &__link {
    padding: 5px 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $white-color;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: $trnstn300ms;

    @include landscapeMobile {
      padding: 0;
      font-size: 24px;
      line-height: 24px;
    }

    &:hover {
      text-decoration-color: $white-color;
    }

  }

  &__phone-number {
    font-size: 12px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $white-color;
    margin-bottom: 16px;

    @include landscapeMobile {
      margin-bottom: 8px;
    }
  }

  &__phone-title {
    padding: 5px 0;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $white-color;
    margin-bottom: 8px;

    &::after {
      content: "";
      color: $white-color;
      width: 111px;
      height: 0.5px;

    }
  }
}
