.card {
  display: flex;
  flex-direction: column;
  margin-bottom: 48px;
  align-items: center;
  width: 270px;

  &__photo {
    width: 153px;
    height: 153px;
    margin-bottom: 28px;
    border-radius: 50%;
    transition: transform $trnstn300ms;
    cursor: pointer;

    &:hover {
      transform: scale(1.2);
    }
  }
  &__name {
    font-weight: bold;
    font-size: 24px;
    margin-bottom: 4px;
    line-height: 30px;
    text-align: center;
    color: $blue-color;
    transition: opacity $trnstn300ms;

    &:hover {
      opacity: 0.6;
    }
  }
  &__subtitle {
    font-weight: normal;
    font-size: 14px;
    margin-bottom: 4px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }
  &__review {
    font-weight: normal;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: #3e3e3e;
  }
}
