.apart-card {

  &__pictures {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: 131px;
    margin-bottom: 16px;
    gap: 19px;

    &--2-1 {
      @include onDesktop {
        grid-template-columns: 2fr 1fr;
        grid-template-rows: 256px;
      }
    }

    &--1-1 {
      @include onDesktop {
        grid-template-rows: 256px;
      }
    }

    &--1-2 {
      @include onDesktop {
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 256px;
      }
    }
  }

  &__photo {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 8px;
    transition: transform $trnstn300ms;
    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }
  }
  &__title {
    margin-bottom: 32px;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;

  }

  &__link {
    color: $blue-color;
    font-weight: 700;
    text-decoration: none;
    transition: color $trnstn300ms;

    &:hover {
      color: $dark-blue;
    }
  }
}
