.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  margin-bottom: 32px;

  &__name {
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__icon {
    width: 23px;
    height: 16px;
  }
}
